import React, { Component } from "react";
import { Grid, Row, Col,   } from "react-bootstrap";
import { DateTime, Interval } from "luxon";

import ControlPanel from "./ControlPanel";
import Table from "./Table";
import AddModal from "./AddModal";

class Schedule extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showAddModal: false,
            days: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
            intervals: (function() {
                return Interval.fromDateTimes(DateTime.fromObject({ hours: 6 }), 
                DateTime.fromObject({ hours: 23, minutes: 59 })).splitBy({ hours: 1 })
                // let i = 0;
                // let times = [];
                // let startTime = DateTime.fromObject({ hours: 6 });
                // for(; i < 7; i++) {
                //     times.push(startTime.plus({ hours: 1 }));
                // }
                // return times;
            })()
        }
        this.toggleModal = this.toggleModal.bind(this);
    }

    toggleModal() {
        this.setState((state) => {
            console.log("state", state);
            return Object.assign({}, state, { showAddModal: !state.showAddModal }); 
        });
    }


    render() {
        return (
            <div>
                <div className="panel panel-default">
                    <div className="panel-body">
                    <showAddModal toggleModal={this.toggleModal} show={this.state.showAddModal} />
                    <ControlPanel toggleModal={this.toggleModal} selectTerm={this.props.selectTerm} terms={this.props.terms} />
                    <Table days={this.state.days} intervals={this.state.intervals} />
                    </div>
                </div>
            </div>
        )
    }


}

export default Schedule;





